	module.exports = {"data":{"markdownRemark":{"html":"<p>You can export your reports as:</p>\n<ul>\n<li>.csv files</li>\n<li>markdown files</li>\n<li>.zip files</li>\n</ul>\n<p>You can also export reports through utilizing the API. See these articles from the HackerOne API documentation to learn more:</p>\n<ul>\n<li><a href=\"https://api.hackerone.com/docs/v1#/reports/read\">https://api.hackerone.com/docs/v1#/reports/read</a></li>\n<li><a href=\"https://api.hackerone.com/docs/v1#/reports/query\">https://api.hackerone.com/docs/v1#/reports/query</a></li>\n</ul>\n<h3>Export to .csv Files</h3>\n<p>You can download reports as .csv files to enable you to quickly generate a spreadsheet of selected reports with key details. The .csv files don't include the comments. The downloaded file provides the:</p>\n<ul>\n<li>Report ID</li>\n<li>Title</li>\n<li>Severity rating</li>\n<li>Severity score</li>\n<li>State</li>\n<li>Sub-state</li>\n<li>Weakness</li>\n<li>Time the report was reported</li>\n<li>Time of first response</li>\n<li>Time of triage time</li>\n<li>Time the report was closed</li>\n<li>Time the hacker was awarded with the award</li>\n<li>Security member assigned to the report</li>\n<li>Hacker that submitted the report</li>\n<li>Bounty</li>\n<li>Bonus</li>\n<li>Whether the report is public or private</li>\n<li>Reference</li>\n<li>Reference URL</li>\n<li>Structured scope</li>\n</ul>\n<p>To export reports as a .csv file:</p>\n<ol>\n<li>Select the check box of the reports you'd like to export in your inbox. You can select as many reports as you'd like.</li>\n<li>Click the <strong>Download as CSV</strong> link to download the selected reports.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/export-reports-9c07db4450e139a6032ee25c2f41b2d7-b3f6c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 41.391941391941394%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"export-reports\"\n        title=\"\"\n        src=\"/static/export-reports-9c07db4450e139a6032ee25c2f41b2d7-e7ffa.png\"\n        srcset=\"/static/export-reports-9c07db4450e139a6032ee25c2f41b2d7-49813.png 125w,\n/static/export-reports-9c07db4450e139a6032ee25c2f41b2d7-dc8a5.png 250w,\n/static/export-reports-9c07db4450e139a6032ee25c2f41b2d7-e7ffa.png 500w,\n/static/export-reports-9c07db4450e139a6032ee25c2f41b2d7-dda24.png 750w,\n/static/export-reports-9c07db4450e139a6032ee25c2f41b2d7-c5f18.png 1000w,\n/static/export-reports-9c07db4450e139a6032ee25c2f41b2d7-b3f6c.png 1092w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Export to Markdown or .zip Files</h3>\n<p>To export reports as markdown or .zip files:</p>\n<ol>\n<li>Select the report you want to export from your inbox.</li>\n<li>Click <b>Export</b> within the report.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/export-reports-2-10fb09fbc22411e11c3d1f41bc56d7c9-02d17.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 24.313725490196074%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"export-reports-2\"\n        title=\"\"\n        src=\"/static/export-reports-2-10fb09fbc22411e11c3d1f41bc56d7c9-e7ffa.png\"\n        srcset=\"/static/export-reports-2-10fb09fbc22411e11c3d1f41bc56d7c9-49813.png 125w,\n/static/export-reports-2-10fb09fbc22411e11c3d1f41bc56d7c9-dc8a5.png 250w,\n/static/export-reports-2-10fb09fbc22411e11c3d1f41bc56d7c9-e7ffa.png 500w,\n/static/export-reports-2-10fb09fbc22411e11c3d1f41bc56d7c9-dda24.png 750w,\n/static/export-reports-2-10fb09fbc22411e11c3d1f41bc56d7c9-c5f18.png 1000w,\n/static/export-reports-2-10fb09fbc22411e11c3d1f41bc56d7c9-02d17.png 1020w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"3\">\n<li>Select the type of file you want to export the report as. You can choose from:</li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Details</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>View raw text</td>\n<td>You can copy and paste details of the report in markdown.</td>\n</tr>\n<tr>\n<td>Export as .zip</td>\n<td>You can download the complete report including the attachments as a zip archive.</td>\n</tr>\n</tbody>\n</table>\n<ol start=\"4\">\n<li><i>(Optional)</i> Select whether you want to <b>include internal activities</b>.</li>\n</ol>","frontmatter":{"path":"/programs/export-reports.html","id":"programs/export-reports","title":"Exporting Reports","description":null}}},"pathContext":{}}

